import React from "react";
import Img1 from "../Logos/app.png";
import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepLabel from "@mui/material/StepLabel";
import StepContent from "@mui/material/StepContent";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";

const steps = [
  {
    label: "Install Fuelcab App",
    description: `For each ad campaign that you create, you can control how much`,
  },
  {
    label: "Login With Your Information",
    description:
      "An ad group contains one or more ads which target a shared set of keywords.",
  },
  {
    label: "Place Your Order & Then Relax",
    description: `Try out different ad text to see what brings in the most customers,
              `,
  },
];

export default function VerticalLinearStepper() {
  const [activeStep, setActiveStep] = React.useState(0);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div className="mx-3 mb-20" id="Fuel_App">
      <div className="flex flex-col items-center mt-20 ">
        <h3 className="text-center text-sm md:text-base" style={{ color: "black" }}>
          FUELCAB TECH FEATURES
        </h3>
        <h4 className="text-center text-2xl md:text-3xl " style={{ color: "#a57734" }}>
          Its time to go with smart fuelling
        </h4>
      </div>
      <section className="flex justify-center items-center">
        <div className="mt-4 grid items-center grid-col-1 lg:grid-cols-2">
          <section className=" w-full h-auto">
            <img className="comp_img1" src={Img1} alt="img" />
          </section>
          <section className="mx-12 lg:mx-2 md:mx-28">
            <Box >
              <Stepper activeStep={activeStep} orientation="vertical">
                {steps.map((step, index) => (
                  <Step key={step.label}>
                    <StepLabel
                      optional={
                        index === 2 ? (
                          <Typography variant="caption">Last step</Typography>
                        ) : null
                      }
                    >
                      {step.label}
                    </StepLabel>
                    <StepContent>
                      <Typography>{step.description}</Typography>
                      <Box sx={{ mb: 2 }}>
                        <div>
                          <Button
                            variant="contained"
                            style={{ backgroundColor: "#162839" }}
                            onClick={handleNext}
                            sx={{ mt: 1, mr: 1 }}
                          >
                            {index === steps.length - 1 ? "Finish" : "Continue"}
                          </Button>
                          <Button
                            disabled={index === 0}
                            variant="contained"
                            onClick={handleBack}
                            sx={{ mt: 1, mr: 1 }}
                            style={{ backgroundColor: "#52bdc5" }}
                          >
                            Back
                          </Button>
                        </div>
                      </Box>
                    </StepContent>
                  </Step>
                ))}
              </Stepper>
              {activeStep === steps.length && (
                <Paper square elevation={0} sx={{ p: 3 }}>
                  <Typography>
                    All steps completed - you&apos;re finished
                  </Typography>
                  <Button onClick={handleReset} sx={{ mt: 1, mr: 1 }}>
                    Reset
                  </Button>
                </Paper>
              )}
            </Box>
          </section>
        </div>
      </section>
    </div>
  );
}
