import React, { useState, useEffect } from "react";
// import Button from "react-bootstrap/Button";
import Comp2 from "./Comp1_2";
import Comp3 from "./Comp1_3";
import Comp4 from "./Comp1_4";
import Comp5 from "./Comp1_5";
