import Stepper from "./OrderForm/Stepper";
import StepperControls from "./OrderForm/StepperControls";
import { StepperContext } from "../context/stepperContext";
import React, { useState } from "react";
import Navbar from "./Navbar_other";
import Account from "./OrderForm/Steps/Account";
import Payment from "./OrderForm/Steps/Payment";
import Details from "./OrderForm/Steps/Details";
import Final from "./OrderForm/Steps/Final";

function Order() {
  const steps = [
    "Account Information",
    "Personal Details",
    "Payment",
    "Complete",
  ];
  const [userData, setUserData] = useState("");
  const [finalData, setFinalData] = useState([]);
  const [currentStep, SetCurrentStep] = useState(1);
  const displaySteps = (step) => {
    switch (step) {
      case 1:
        return <Account />;

      case 2:
        return <Details />;

      case 3:
        return <Payment />;

      case 4:
        return <Final />;
    }
  };

  const handleClick = (direction) => {
    let newStep = currentStep;
    direction === "next" ? newStep++ : newStep--;
    newStep > 0 && newStep <= steps.length && SetCurrentStep(newStep);
  };
  return (
    <div className="mt-28 mb-14">
      <div className="flex flex-col items-center mt-12 mx-2">  
        <h3 className="text-center text-lg" tyle={{ color: "black" }}>
          Place Your Order Here
        </h3>
        <h4 className="text-center text-3xl " style={{ color: "#a57734" }}>
          We Are Provide The Safe Method For Payment
        </h4>
      </div>
      <Navbar />
      <div className="w-full md:w-1/2 mx-auto shadow-xl shadow-rose-100 rounded-2xl pb-2 bg-transparent  border-2 border-gray-100 md:p-6 mt-4">
        {/* Stepper */}
        <div className="container horizontal mt-2">
          <Stepper steps={steps} currentStep={currentStep} />

          {/* display conponents */}
          <div className="my-10 p-10">
            <StepperContext.Provider
              value={{
                userData,
                setUserData,
                finalData,
                setFinalData,
              }}
            >
              {displaySteps(currentStep)}
              
            </StepperContext.Provider>
          </div>
        </div>
        {/* Navigation Controls */}
        {currentStep != steps.length && (
          <StepperControls
            handleClick={handleClick}
            currentStep={currentStep}
            steps={steps}
          />
        )}
      </div>
    </div>
  );
}

export default Order;
