import React from "react";
import Navbar from "./Navbar_other";
function Payment_Refund() {
  return (
    <div>
      <Navbar />
      <div className="mt-32">
        <h2 className="text-center text-base" style={{ color: "black" }}>
          REFUND AND CANCELLATION POLICY
        </h2>
        <h2 className="text-center text-3xl " style={{ color: "#a57734" }}>
          You Can Read Below Lines
        </h2>
        <div className="flex justify-center mt-12">
          <div className="w-11/12 md:w-7/12 flex justify-center flex-col items-center">
            <div className="flex flex-col items-start w-full mt-4">
              <h2>Refund Policy</h2>
              <p className="text-xl">
                Once we receive your refund request for cancellation and refund,
                we will scrutinize and evaluate the transaction, after approval
                refund will initiate. The refund amount will be calculated by
                deducting Internet payment charges from your original payment.
              </p>
              <p className="text-xl my-2">
                You will receive the credit within a certain amount of days,
                depending on your card issuer's policies. Refunds will be issued
                to the same payment method which was used at the time of
                purchase.
              </p>
              <p className="text-xl my-2">
                Debit card/ Credit Card  5 to 7 working days Net Banking  3 to
                5 working days If the standard time-frame as mentioned above has
                passed and you have still not received the refund, please
                contact your credit or debit card issuer or your bank for more
                information.
              </p>
              <p className="text-xl my-2">
                If you have any questions about our Returns and Refunds Policy,
                please contact us by email at info@fuelcab.com or call us on
                +91-9988909052.
              </p>
            </div>
            <div className="flex flex-col items-start w-full mt-4">
              <h2>Cancellation Policy</h2>
              <p className="text-xl my-2">
                In case the Order is canceled before dispatch, no cancellation
                charges shall be leviable but in case the same is canceled
                thereafter then a cancellation fee shall be payable by the end
                customer to the Dealer. In case post confirmation of the order,
                the dealer fails to show up and deliver the product, then
                FuelCab shall not be responsible to either party for
                cancellation of the order or its supply in any eventuality. If
                during the process of delivery, the dealer, its agent or
                representative fails to comply with or adhere to any of the
                applicable rules, etc then the end customer shall be within its
                right to cancel the order immediately without any further
                charges, except to pay for the product already received by the
                end customer.
              </p>
            </div>
          </div>
        </div>
      </div>
      <div className="mt-12"></div>
    </div>
  );
}

export default Payment_Refund;
