import React, { Component } from "react";
import Col1 from "../Images/delivery service.jpg";
import Col2 from "../Images/Earth movers.jpg";
import Col3 from "../Images/farm.jpg";
import Col4 from "../Images/generators.jpg";
import Col5 from "../Images/hospital.jpg";
import Col6 from "../Images/schoolbus.jpg";

export class comp1_3 extends Component {
  render() {
    return (
      <div id="Serve">
        <div className="flex flex-col items-center mt-12">
          <h3
            className="text-center text-sm md:text-base"
            style={{ color: "black" }}
          >
            WHO WE SERVE
          </h3>
          <h4
            className="text-center text-2xl md:text-3xl "
            style={{ color: "#a57734" }}
          >
            Serving 80+ institutions
          </h4>
        </div>
        <div className="flex justify-center items-center xl:flex-row lg: flex-col">
          <div className="flex flex-col items-center mt-12">
            {/* <h4 className="">Earth Movers</h4> */}
            <div className="container m-2 hover01 column mx-1">
              <div className="container2">
                <section className="figure">
                  <img className="img1 big_img " alt="" src={Col2} />
                  <p className="z-50">hello we are here</p>
                </section>
              </div>
            </div>

            <div className="flex">
              {/* <h4 className="">Generators</h4> */}
              <div className="container  mt-4 hover01 column">
                <div className="container2_1">
                  <figure>
                    <img className="img1 small_img " alt="" src={Col4} />
                  </figure>
                </div>
              </div>

              {/* <h4 className="">Hospital</h4> */}
              <div className="container mt-4 hover01 column">
                <div className="container2_1">
                  <figure>
                    <img className="img1 small_img " alt="" src={Col5} />
                  </figure>
                </div>
              </div>
            </div>
          </div>

          <div className="flex flex-col items-center mt-12 ">
            <div className="flex">
              {/* <h4 className="">Delivery Service</h4> */}
              <div className="container mt-2 hover01 column">
                <div className="container2_1">
                  <figure>
                    <img className="img1 small_img " alt="" src={Col1} />
                  </figure>
                </div>
              </div>
              {/* <h4 className="">School Bus</h4> */}
              <div className="container  mt-2 hover01 column">
                <div className="container2_1">
                  <figure>
                    <img className=" img1 small_img " alt="" src={Col6} />
                  </figure>
                </div>
              </div>
            </div>
            {/* <h4 className="">Farm</h4> */}
            <div className="container mt-7 hover01 column">
              <div className="container2">
                <figure className="figure">
                  <img className="img1 big_img " alt="" src={Col3} />
                </figure>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default comp1_3;
