import React from "react";
import { MDBFooter } from "mdb-react-ui-kit";
import Button from "react-bootstrap/Button";
import { HashLink } from "react-router-hash-link";
import { Link } from "react-router-dom";
//images
import face from "../Images/facebook.png";
import insta from "../Images/instagram.png";
import tele from "../Images/telegram.png";
import logo from "../Logos/Main_logo.png";

export default function App() {
  return (
    <MDBFooter
      style={{ backgroundColor: "#2a486c" }}
      className="text-center text-lg-start text-muted mt-5 main_body "
    >
      {/* section for become a fuelent */}
      <div className="border-b-2 border-cyan-400">
        <section className="d-flex justify-content-center justify-content-lg p-4">
          <div className="me-5 d-none d-lg-block">
            <span className="text-gray-300">
              Become a fuelent, take our franchise now:
            </span>
          </div>

          <div className="flex">
            <Button variant="warning" className="-mt-2 mx-3">
              Complete Info
            </Button>{" "}
            <Button variant="danger" className="-mt-2 mx-3">
              Join Us
            </Button>{" "}
          </div>
        </section>
      </div>
      <div className="border-b-2 border-cyan-400">
        <section className="d-flex justify-content-center justify-content-lg p-4 ">
          <div className="me-5 d-none d-lg-block">
            <span className="text-gray-300">
              Get connected with us on social networks:
            </span>
          </div>

          <div className="flex">
            <a href="/" className="me-4 text-reset">
              <img className="w-8" src={face} alt="" />
            </a>
            <a href="/" className="me-4 text-reset">
              <img className="w-8" src={insta} alt="" />
            </a>
            <a href="/" className="me-4 text-reset">
              <img className="w-8" src={tele} alt="" />
            </a>
          </div>
        </section>
      </div>

      <section className="text-gray-300">
        <div className="container text-center  text-md-start  mt-5">
          <div className="row mt-3 ">
            <div className="col-md-3 col-lg-4 col-xl-3 mx-auto mb-4">
              <h6 className="text-uppercase fw-bold mb-4 flex justify-center">
                <h4 className="fas fa-gem me-7 text-sky-500">
                  <img className="w-36" src={logo} alt="img" />
                </h4>
              </h6>
              <p>
                Here you can use rows and columns to organize your footer
                content.
              </p>
            </div>

            <div className="col-md-2 col-lg-2 col-xl-2 mx-auto mb-4">
              <h6 className="text-uppercase fw-bold mb-4 text-sky-500 ">
                Products
              </h6>
              <p>
                <HashLink to="#Serve" smooth className=" text-white">
                  Who We Serve{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#why_fuelcab" smooth className=" text-white">
                  Why Fuelcab{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#Services" smooth className=" text-white">
                  Our Services{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#Testimonial" smooth className=" text-white">
                  Our Testimonial{" "}
                </HashLink>
              </p>
            </div>

            <div className="col-md-3 col-lg-2 col-xl-2 mx-auto mb-4">
              <h6 className="text-uppercase fw-bold mb-4 text-sky-500">
                Useful links
              </h6>
              <p>
                <HashLink to="#Home" smooth className=" text-white">
                  Home{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#why_fuelcab" smooth className=" text-white">
                  Why Fuelcab{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#Services" smooth className=" text-white">
                  Our Service{" "}
                </HashLink>
              </p>
              <p>
                <HashLink to="#Fuel_App" smooth className=" text-white">
                  Fuelcab App{" "}
                </HashLink>
              </p>
            </div>

            <div className="col-md-4 col-lg-3 col-xl-3 mx-auto mb-md-0 mb-4">
              <h6 className="text-uppercase fw-bold mb-4 mx-3 text-sky-500">
                Contact
              </h6>
              <p>
                <i className="fas fa-home me-3"></i> Rorkee, NY 10012, US
              </p>
              <p>
                <i className="fas fa-envelope me-3"></i>
                info@Fuelcab.com
              </p>
              <p>
                <i className="fas fa-phone me-3"></i> + 91 9988909092
              </p>
              <p>
                <i className="fas fa-print me-3"></i> + 91 9520348410
              </p>
            </div>
          </div>
        </div>
      </section>

      <div
        className="text-center p-4 "
        style={{ backgroundColor: "rgba(0, 0, 0, 0.05)" }}
      >
        <Link
          to="/Privacy"
          className="no-underline text-gray-500 hover:no-underline hover:text-gray-500"
        >
          PrivacyPolicy 2022 Copyright:
        </Link>
        <Link className="text-reset fw-bold" to="/">
          Fuelcab.com
        </Link>
        <div>Design and Developed by: Harsh Saxena</div>
      </div>
    </MDBFooter>
  );
}
